.content--sponsor {
	position: relative;
	display: grid;
	grid-template-areas: 'sponsor';
	grid-template-rows: 100%;
	grid-template-columns: 100%;
	text-align: center;
	padding: 5vh 0;
	align-items: end;
    justify-items: center;
}

.content__title {
	font-family: ivypresto-display, serif;
	font-weight: 300;
	margin-left: 70px;
	position: relative;
	font-size: 2.778rem;
}



.content__title--sponsor {
	grid-area: sponsor;
	font-size: 9vw;
	line-height: 0.7;
	color: var(--color-page);
	font-weight: 100;
	padding-bottom: 17vw;
}

.content__title--sponsor strong {
	display: block;
	font-size: 14vw;
	font-weight: 400;
}

.content__subtitle {
	margin-left: 110px;
	align-self: end;
    font-size:larger;
	padding: 0.2rem 0 0 1.5rem;
}

.content__foot {
	text-align: center;
	color: var(--color-info);
}

.frame {
	grid-area: sponsor;
	width: 30vw;
	min-width: 200px;
	max-width: 350px;
	background: var(--color-page);
	padding: 5rem 2rem;
}

.panel {
	margin: 2rem auto;
}

.js .panel {
	width: calc(100% - 7rem);
	display: grid;
	grid-template-areas: 'item';
}

.panel__item {
	position: relative;
	grid-area: item;
	width: 100%;
	display: grid;
	grid-gap: 4rem;
	grid-template-columns: auto minmax(1fr) auto;
	grid-template-areas: 'image title title'
		'image title title'
		'subtitle subtitle subtitle'
		'... ... close';
	grid-template-rows: fr auto auto auto;
	
}

.no-js .panel__item {
	margin: 3rem 0 5rem 0;
	grid-template-areas: 'image title title'
		'image title title'
		'subtitle subtitle subtitle';
}

.js .panel__item {
	pointer-events: none;
	margin-left: 20%;
}

.panel__item-imgwrap {
	grid-area: image;
	position: relative;
	border-radius: 0.35rem;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	align-self: start;
	width:fit-content;
	align-items: flex-start;
	gap: 40px;
	
}

.js .panel__item-imgwrap {
	cursor: none;
	pointer-events: auto;
	will-change: transform, opacity;
}

.panel__item-img {
	position: relative;
	width: 60vw;
	height: auto;
	max-width: 500px;
	display: block;

	
}

.panel__item-title {
	grid-area: title;
	font-size: 5vw;
	margin-left: 30px;
	line-height: 0.65;
	font-family: ivypresto-display, serif;
	font-weight: 100;
}

.panel__item-title span {
	display: inline-block;
	padding-bottom: 1.85vw;
    font-weight: 200;
	will-change: transform, opacity;
	padding-left: 50px;
}

.panel__item-title span:first-child {
	font-weight: 500;
    color: #00D47E;
	padding-left:0;
}



.panel__item-title span:last-child {
	font-weight: 400;
	text-indent: 3vw;
	display: block;
	padding-left: 60px;
}


.panel__item-subtitle h4 {
	position: relative;
	margin: 2rem 0 5rem 0;
	font-weight: normal;
	font-size: 3rem;
	font-size: clamp(2rem,8vw,1.5rem);
}

.panel__item-subtitle h4 span.char {
	will-change: transform, opacity;
}

.panel__item-close {
	grid-area: close;
	border: 0;
	padding: 70px, 50px, 50px, 0;
	margin: 0;
	position: relative;
	background: none;
	cursor: none;
	align-self: end;
}

.no-js .panel__item-close {
	display: none;
}

.panel__item-close:focus {
	outline: none;
}

