@media only screen and (max-width: 767px) {
    .wh_tile > div{
        height: auto;
    }
}


@media screen and (min-width: 760px) {
	.panel__item {
		grid-gap: 2rem;
		grid-template-columns: auto minmax(0,1fr) auto;
		grid-template-areas: 'image title title'
			'image subtitle close';
		grid-template-rows: 1fr auto;
	}
	
	.no-js .panel__item {
		margin: 0 0 5rem 0;
		grid-template-areas: 'image title title'
			'image subtitle subtitle';
	}
}

@media screen and (min-width: 1170px) {
	.panel__item {
		grid-template-columns: auto minmax(0,1fr) 80px;
		grid-template-areas: 'image title close'
			'image subtitle close';
		grid-template-rows: 1fr auto;
	}

	.panel__item-close {
		align-self: unset;
	}

	.panel__item-close span {
		display: none;
	}

	.panel__item-close::before,
	.panel__item-close::after {
		content: '';
		width: 1px;
		height: 100%;
		background: currentColor;
		position: absolute;
		transform-origin: 50% 50%;
		left: 50%;
		top: 0;
		transform: scale(1.15) rotate(32deg);
	}

	.panel__item-close::after {
		transform: scale(1.15) rotate(-32deg);
	}
}

.js .panel__item-title>*,
.js .panel__item-subtitle>*,
.js .panel__item-close {
	opacity: 0;
	pointer-events: none;
}

.js .panel__item--open .panel__item-title>*,
.js .panel__item--open .panel__item-subtitle>*,
.js .panel__item--open .panel__item-close {
	pointer-events: auto;
}

.js .panel__item--open {
	z-index: 1000;
}

@media screen and (min-width: 767) {
	.myTopButton{
		text-align: right;
		display: grid;
		align-content: space-between;
		width: 100%;
		max-width: calc(1450px - 4rem);
		grid-template-columns: 95% 6% 5% 5%;
		grid-template-rows: auto;
		grid-template-areas: 'title links addon';
	}



	.page--header {
	margin-bottom: 0px;
    width: 100vw;
    position: relative;
    font-size: larger;
    font-weight: bold;
    display: flex;
    background: var(--color-page);
	backdrop-filter: blur(30px);
    top:0;
    z-index: 1;
	}

	.page--footer {
		padding: 6rem 4rem 1.5rem;
		border-radius: 8px 8px 0 0;
		margin: 5vh auto 0;
	}

	.page__title {
		grid-area: title;
		margin: 0;
	}

	.page__links {
		grid-area: links;
		padding: 0;
		justify-self: end;
		align-self: end;
	}


	.page__addon {
		margin: 0;
		grid-area: addon;
		justify-self: end;
	}
}

@media (any-pointer:fine) {
	.cursor {
		position: fixed;
		top: 0;
		left: 0;
		display: block;
		pointer-events: none;
		z-index: 1001;
	}

	.cursor__inner {
		fill: var(--cursor-fill);
		stroke: var(--cursor-stroke);
		stroke-width: var(--cursor-stroke-width);
		opacity: 0.7;
	}

	.no-js .cursor {
		display: none;
	}

}
